<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
    <v-container >
        <h2>Contact form</h2>

        <v-expansion-panels>
            <v-expansion-panel hover focusable inset >
                <v-expansion-panel-header>
                    <p>
                        If you wish to contact me and I've bailed out of any and all social media, or you have, or we just don't have an overlap
                        in such more “conventional” ways of communication, you may use the form below. Do include a contact form of your own if I
                        am to get back to you!
                    </p>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <p>
                        This form may not work at times due to stringent anti-spam measures; if it's disabled or errors out, refresh the page later.
                        And when it does, I may not immediately receive your message; it'll be in a database somewhere.
                    </p>
                    <p>
                        You may find me on things like (in decreasing order of preference):
                    </p>
                    <ul>
                        <li>Discord</li>
                        <li>Signal</li>
                        <li>generic email</li>
                        <li>LinkedIn</li>
                        <li>WhatsApp</li>
                        <li>(literally anything else)</li>
                        <li>Facebook</li>
                    </ul>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <p></p>

        <span v-if="backendPresent">
            <v-text-field filled outlined v-model="title" label="Title" :disabled="!formElementsEnabled" />

            <v-textarea filled outlined v-model="content" label="Message" :disabled="!formElementsEnabled" />

            <v-file-input small-chips v-model="file" label="Upload file" />

            <v-btn :disabled="!formElementsEnabled" :loading="restCallsGoing > 0"
                   width="inherit" elevation="7" x-large
                   @click="submitMessage()">
                Send
            </v-btn>
        </span>

        <p v-else>
            The contact form is not available from here. Go to the
            <a href="https://teonistor.herokuapp.com/#/contact"> full version of the website </a>
            which has a backend to receive it.
        </p>

        <Notifications :alerts="alerts" :floating="true" />
    </v-container>
</template>

<script>
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {
      Notifications
    },

    mixins: [AxiosHelper],

    props: ['backendPresent'],

    data: () => ({
      formEnabled: false,

      title: '',
      content: '',
      file: null
    }),

    computed: {
      formElementsEnabled () {
        return this.formEnabled && !this.restCallsGoing;
      },

      message () {
        return JSON.stringify({
          title: this.title,
          content: this.content,
          receivedFrom: location.host
        });
      }
    },

    methods: {
      submitMessage () {
        let formData = new FormData();
        formData.append('message', this.message);
        if (this.file)
          formData.append('file', this.file, this.file.name);

        this.credentialedAxios('submitting form', axios => axios.post(this.$apiUrl + '/contact', formData),
            ignore => this.alerts.push({type: 'info', text: 'Got it!'}))
          .then(() => this.checkEnabled());
      },

      checkEnabled() {
        this.credentialedAxios('checking whether the form is enabled', axios => axios.get(this.$apiUrl + '/contact/enabled'),
          data => this.formEnabled = data);
      }
    },

    mounted () {
      // TODO Use a component or something to prevent this check when there's no backend
      this.checkEnabled();
    }
  }
</script>

